* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --color-131921: #131921;
  --color-3b4149: #3b4149;  
  --color-febd69: #febd69;
  --color-232f3e: #232f3e;
  --color-bf4b80: #d86c2e;
  --color-f5f5f7: #f5f5f7;
  --color-ededed: #ededed;
  --color-777777: #777777;
  --color-1c1c1b: #1c1c1b;
}

.section-heading {
  font-size: 25px;
  line-height: 32px;
  font-weight: 500;
  margin-bottom: 30px;
  text-decoration: none; /* Untuk menghilangkan underline */
}

.card-wrapper {
  background-color: white;
  box-shadow: 0 0 10px black;
  padding: 15px;
}

body {
  font-family: 'Rublik', sans-serif;
}

a {
  text-align: none;
  color: #70a8e7;
  display:  inline-block;
  text-decoration: none;
}

.form-control:focus {
  box-shadow: none;
  border-color: inherit;
}

.gap-10 {
  gap: 10px;
}

.gap-15 {
  gap: 15px;
}

.gap-30 {
  gap: 30px;
}

.button {
  background-color: var(--color-232f3e); /* Warna default */
  color: white;
  font-size: 14px;
  padding: 13px 33px;
  border-radius: 35px;
  transition: all 0.3s ease; /* Efek transisi halus */
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2); /* Efek bayangan default */
}

.button:hover {
  background-color: var(--color-febd69); /* Warna saat hover */
  color: #131921; /* Ubah warna teks saat hover */
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.4); /* Bayangan lebih besar saat hover */
  transform: translateY(-5px); /* Efek pergerakan naik saat hover */
}

/*  Header part */

.header-top-strip {
 border-bottom: 1px solid var(--color-3b4149);
 background-color: var(--color-232f3e) 
}

.header-upper {
  background-color: var(--color-131921);
}

.header-bottom {
  background-color: var(--color-3b4149);
}

.header-upper .input-group-text {
  background-color: var(--color-febd69);
}

.header-upper .header-upper-links img {
  width: 35px;
  height: 35px;
}

.header-bottom .menu-links a
.header-bottom .dropdown button {
  color: white;
  font-size: 14px;
  line-height: 15px;
  font-weight: 400;
  letter-spacing: 0.3;
  text-transform: uppercase;
  text-decoration: none; /* Untuk menghilangkan underline */
}

.header-bottom .dropdown button:focus {
  box-sizing: none;
}

.header-bottom .dropdown-menu {
  background-color: var(--color-131921);
  width: 100%;
  transform: translate3d(0px, 57px, 0) important;
}

.header-bottom .dropdown-item {
  padding: 20px;
  margin-bottom: 3px;
  border-bottom: 1px solid var(--color-3b4149);
}

.header-bottom .dropdown-item:hover {
  background-color: transparent;
  color: var(--color-febd69);
}

.header-upper-links-d-align-items-center {
  display: flex; /* Menjadikan kontainer menggunakan flexbox */
  align-items: center; /* Mengatur item untuk berada di tengah secara vertikal */
  justify-content: space-between; /* Mengatur item agar tersebar rata */
}

.header-upper-links-d-align-items-center > div {
  display: flex; /* Jika item dalam div juga perlu menggunakan flexbox */
  align-items: center; /* Mengatur agar ikon dan teks sejajar */
}

/* header part end */

/* fotter part */

footer {
  background-color: var(--color-232f3e);
}

footer:not(:first-child) {
  border-top: 1px rgba(255, 255, 255, 0.1);
}

footer .input-group-text {
  background-color: var(--color-232f3e);
  color: white;
  padding: 8px 17px;
}

/* footer part end */

/* home page */

.main-banner-content {
  top: 15%;
  left: 5%;
}

.main-bannner-content h4 {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: var(--color-bf4b80);
  margin: 0 0 12px;
  letter-spacing: 0.3px;
  text-transform: uppercase;
}

.main-bannner-content h1 {
  font-size: 42px bold;
  line-height: 52px;
  letter-spacing: -2px;
  font-weight: 500;
  text-transform: none;
}

.main-bannner-content p {
  font-size: 18px;
  line-height: 20px;
  letter-spacing: 0.4;
  color: var(--color-131921);
}

.small-banner-content {
  top: 25%;
  left: 10%;
}

.small-bannner-content h4 {
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  color: var(--color-bf4b80);
  margin: 0 0 12px;
  letter-spacing: 0.3px;
  text-transform: uppercase;
}

.small-bannner-content h5 {
  font-size: 22px bold;
  line-height: 34px;
  letter-spacing: -2px;
  font-weight: 500;
  text-transform: none;
}

.small-bannner-content p {
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.4;
  color: var(--color-131921);
}

.small-banner {
  width: 49%;
}

/* home wrapper */

.home-wrapper-2 {
  background-color: var(--color-f5f5f7);
}

/* home wrapper end */

/* categories sections */

.categories {
  background-color: white;
  box-shadow: 0 0 10px black;
  padding: 15px;
}

.categories > div {
  width: 25%;
  padding: 10px 10px 10px 20px;
}

.categories > div > div {
  width: 60%;
}

.categories > div:nth-child(1),
.categories > div:nth-child(2),
.categories > div:nth-child(3),
.categories > div:nth-child(4) {
  border-bottom: 1px solid var(--color-ededed);
}

.categories > div:nth-child(1),
.categories > div:nth-child(2),
.categories > div:nth-child(3) {
  border-right: 1px solid var(--color-ededed);
}

.categories > div:nth-child(5),
.categories > div:nth-child(6),
.categories > div:nth-child(7) {
  border-right: 1px solid var(--color-ededed);
}

/* categories end */

/* home page end */

/* Blog Part */

.Blog-card {
  background-color: white;
  border-radius: 10px;
}

.Blog-card img {
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}

.blog-content {
  padding: 15px;
}

.blog-contentp.date {
  font-size: 13px;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 0;
  font-weight: 400;
  color: var(--color-777777);
  padding: 0;
}

.blog-content h5 {
  font-size: 15px;
  color: var(--color-131921);
}

.blog-content p.desc {
  font-size: 13px;
  line-height: 22px;
  color: var(--color-777777);
}

.single-blog-card a {
  font-size: 24px;
  color: var(--color-777777);
  margin-bottom: 20x;
}

.single-blog-card h3 {
  font-size: 24px;
  font-weight: 600;
  color: var(--color-777777);
}

.single-blog-card p {
  color: var(--color-777777);
  font-size: 14px;
}

/* Blog End */

/* Product Part */

.product-card {
  padding: 15px;
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
}

.product-card .product-image {
  height: 270px;
}

.product-card .product-image img {
  object-fit: contain;
}

.product-card .product-image > img:last-child {
  display: none;
}

.product-card:hover .product-image > img:nth-child(1) {
  display: none;
}

.product-card:hover .product-image > img:last-child {
  display: block;
}


.product-card:hover .action-bar {
  right: 15px;
}

.product-card .product-details h6 {
  color: var(--color-bf4000);
  font-size: 13px;
}

.product-card .product-details h5 {
  font-size: 15px;
  color: var(--color-1c1c1b);
}

.product-card .product-details p.price {
  font-size: 16px;
  color: var(--color-1c1c1b);
}

.product-card p.description {
  font-size: 16px;
  color: var(--color-777777);
  margin-right: 20px;
}

.action-bar {
  top: 10%;
  right: -23px;
  transition: 0.3%;
}

.wishlist-icon {
  top: 2%;
  right: 15px;
}
/* Product Part End */

/* Special Product */

.special-product-card {
  padding: 10px 10px;
  border-radius: 10px;
  background-color: white;
}

/* Special Product End */

/* famous card */

.famous-card .famous-content {
  top: 10%;
  left: 5%;
}
 
.famous-card img {
  border-radius: 10px;
}

.famous-content h5 {
  font-size: 13px;
  line-height: 20px;
  font-weight: 400;
  color: black;
  margin-bottom: 7px;
  text-transform: uppercase;
}

.famous-content h6 {
  font-size: 24px;
  line-height: 38px;
  font-weight: 500;
  color: black;
}

.famous-content p {
  font-size: 16px;
  line-height: 24px;
  font-weight: 100;
  color: black;
}

/* famous card end */

/* Our Store */

.filter-card {
  background-color: white;
  border-radius: 10px;
  padding: 15px 15px 25px;
}

.filter-title {
  font-size: 16px;
  line-height: 20px;
  font-weight: 600;
  color: var(--color-1c1c1b);
  margin-bottom: 20px;
}

.filter-card ul
.filter-card .form-check-label {
  list-style-type: none;
  font-size: 13px;
  line-height: 30px;
  color: var(--color-777777);
  cursor: pointer;
}

.filter-card .form-check-input:focus {
  box-shadow: none;
  border-color: rgba(0, 0, 0, 25);
}

.filter-card .form-check-input:checked {
  background-color: var(--color-febd69);
  border-color: var(--color-febd69);
}

.sub-title {
  font-size: 14px;
  font-weight: 600;
  color: var(--color-1c1c1b);
  margin-bottom: 15px;
  margin-top: 20px;
}

.colors {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.colors li {
  width: 20px;
  height: 20px;
  background-color: red;
  border-radius: 50%;
}

.random-products h5 {
  font-size: 14px;
  margin-bottom: 6px;
}

.random-products:first-child {
  border-bottom: 1px solid var(--color-ededed);
}

.filter-sort-grid {
  padding: 10px;
  background-color: white;
  border-radius: 10px;
}

.grid img {
  width: 25px;
  height: 25px;
  background-color: var(--color-f5f5f7);
  padding: 5px;
  border-radius: 3px;
  cursor: pointer;
}

.gr-12 {
  width: 100%;
}

.gr-6 {
  width: 49%;
}

.gr-4 {
  width: 32%;
}

.gr-3 {
  width: 24%;
}

.gr-12 .product-card {
  display: flex;
  gap: 20px;
}

.gr-6 .product-image {
  width: 49%;
}

.gr-6 .product-image img {
  margin: auto;
  display: block;
}

.gr-12 .product-card .product-image {
  width: 270px;
}

.gr-12 .product-card .product-image {
  height: auto;
}

.gr-12 .action-bar {
  top: 13%;
}


/* Our Store End */

/* Contact Page */

.contact-inner-wrapper {
  padding: 30px 20px;
  border-radius: 15px;
  border-radius: 15px;
  background-color: white;
  gap: 15px;
}

.contact-inner-wrapper > div {
  width: 40%;
}

.contact-title {
  font-size: 26px;
  font-weight: 500;
  line-height: 15px;
  text-align: left;
}

.contact-inner-wrapper form .form-control {
  background-color: var(--color-f5f5f7);
  border-radius: 10px;
  border-color: transparent;
  padding: 10px;
}

.contact-inner-wrapper ul {
  list-style: none;
}

.contact-inner-wrapper address,
.contact-inner-wrapper a,
.contact-inner-wrapper p {
  color: var(--color-777777);
}

/* Contact Page End */

/* Compare product */

.compare-product-card {
  padding: 20px 15px;
  background-color: white;
  border-radius: 10px;
}

.compare-product-card .cross,
.wishlist-card .cross {
  top: 15px;
  right: 15px;
  width: 25px;
  padding: 3px;
  cursor: pointer;
}

.compare-product-details h5.title,
.wishlist-card h5.title {
  font-size: 15px;
  line-height: 22px;
  font-weight: 500;
  color: var(--color-1c1c1b);
}

.compare-product-details h6.price,
.wishlist-card h6.price {
  font-size: 15px;
  line-height: 22px;
  font-weight: 400;
}

.compare-product-card .product-detail {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0px;
  border-top: 1px solid rgba(0, 0, 0, 1);
}

/* Compare product end */

/* auth card */

.auth-card {
  padding: 20px;
  background-color: white;
  width: 500px;
  margin: 30px auto;
  border-radius: 10px;
}

.auth-card h3 {
  font-size: 19px;
  font-weight: 500;
  color: var(--color-131921);
}

.auth-card p {
  font-size: 13px;
  color: var(--color-777777);
}

.auth-card .form-control {
  height: 54px;
  background-color: var(--color-f5f5f7);
  color: var(--color-777777);
  font-size: 14px;
  border-radius: 10px;
  border-color: transparent;
}

.auth-card a {
  font-size: 14px;
  font-weight: 400;
  color: var(--color-1c1c1b);
}

.signup {
  background-color: var(--color-febd69);
  color: var(--color-131921);
  border: none;
}

.signup:hover {
  background-color: var(--color-febd69);
  color: white;
}

/* auth card end */

/* Policy Pages */

.policy {
  background-color: white;
  padding: 30px;
}

/* Policy Pages end */

/* main product */

.description-wrapper h4,
.review-wrapper h3 {
  font-size: 24px;
  color: var(--color-1c1c1b);
  margin-bottom: 10px;
}

.description-wrapper p {
  font-size: 14px;
  color: var(--color-777777);
}

.review-inner-wrapper {
  background-color: white;
  padding: 30px;
}

.review-head {
  border-bottom: 1px solid rgba(0, 0, 0, 1);
  padding-bottom: 20px;
}

.review-head h4 {
  font-size: 18px;
  color: var(--color-1c1c1b);
}

.review-head p,
.review-head a {
  font-size: 14px;
  color: var(--color-777777);
}

.review-form h4 {
  font-size: 15px;
  color: var(--color-777777);
}

.review-form {
  border-bottom: rgba(0, 0, 0, 1);
}

.review p {
  font-size: 14px;
  color: var(--color-777777);
}

.main-product-image {
  padding: 20px;
  background-color: white;
  border-radius: 10px;
}

.main-product-image>div {
  border: 1px solid rgba(0, 0, 0, 1);
}

.main-product-image img {
  width: 100%;
  height: auto;
  max-height: 500px;
  object-fit: cover;
}


.other-product-images {
  background-color: white;
  padding: 20px;
}

.other-product-images img {
  width: 100% !important;
  height: 200px !important;
  object-fit: cover;
}

.other-product-images > div {
  padding: 20px;
  width: 48%;
  border: 1px solid rgba(0, 0, 0, 1);
}

.main-product-details {
  padding: 30px 20px;
  background-color: white;
  border-radius: 10px;
}

.border-bottom {
  border-bottom: 1px solid rgba(0, 0, 0, 0, 25);
}

.main-product-details h3.title {
  font-size: 20px;
  font-weight: 600;
  color: var(--color-1c1c1b);
  margin-bottom: 10px;
}

.main-product-details p.price {
  font-size: 16px;
  line-height: 20px;
  font-weight: 600;
  margin-bottom: 10px;
}

.main-product-details a.review-btn,
.t-review {
  color: var(--color-777777);
  font-size: 13px;
}

.product-heading {
  font-size: 14px;
  color: var(--color-1c1c1b);
  margin-bottom: 0px;
}

.product-data {
  font-size: 13px;
  columns: var(--color-777777);
  margin-bottom: 0px;
}

.main-product-details a {
  font-size: 13px;
  columns: var(--color-777777);
  margin: 0px;
}

/* main product end */

/* Cart Page */

.cart-header,
.cart-data {
  border-bottom: 1px solid rgb(219, 228, 233);
}

.cart-header h4 {
  font-size: 13px;
  color: var(--color-777777);
} 

.cart-col-1 {
  width: 40%;
}

.cart-col-2 {
  width: 10%;
}

.cart-col-3 {
  width: 15%;
}

.cart-col-4 {
  width: 15%;
}

.cart-data h5.price {
  font-size: 18 px;
  color: var(--color-1c1c1b);
}

/* Cart Page End */

/* shipping page */

.total {
  font-size: 14px;
  color: var(--color-1c1c1b);
}

.total-price {
  font-size: 16px;
  color: var(--color-777777);
}


/* shipping page end */